import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider, useAuth } from './context/AuthContext';
import LoginPage from './pages/LoginPage';
import DashboardPage from './pages/DashboardPage';
import OfflineStub from './components/OfflineStub';
import axiosClient from './api/axiosClient';
import LoadingScreen from './components/LoadingScreen';
import { LocalNotifications } from '@capacitor/local-notifications';
import { CircularProgress, Box, CssBaseline, ThemeProvider, createTheme } from '@mui/material';



const theme = createTheme({
    palette: {
        primary: { main: '#1976d2' },
        secondary: { main: '#dc004e' },
        background: { default: '#f4f7f6' },
    },
});

const PrivateRoute: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const { isAuthenticated, loading } = useAuth();
    if (loading) return <LoadingScreen />;
    return isAuthenticated ? <>{children}</> : <Navigate to="/login" />;
};

const AppContent: React.FC = () => {
    const { isAuthenticated, loading } = useAuth();
    const [isOnline, setIsOnline] = useState(navigator.onLine);
    const [isServerAvailable, setIsServerAvailable] = useState(true);

    useEffect(() => {
        const requestPushPermission = async () => {
            // ,      
            if (navigator.userAgent.includes('Android')) {
                const status = await LocalNotifications.requestPermissions();
                console.log('Permission status:', status);
            } else if ('Notification' in window) {
                Notification.requestPermission();
            }
        };

        requestPushPermission();
    }, []);

    useEffect(() => {
        // 1.    Notification    
        if (typeof window !== 'undefined' && 'Notification' in window) {
            Notification.requestPermission().catch((err) =>
                console.warn("   :", err)
            );
        } else {
            console.log("     ");
        }
    }, []);

    useEffect(() => {
        if ('serviceWorker' in navigator) {
            navigator.serviceWorker.addEventListener('controllerchange', () => {
                //      
                window.location.reload();
            });
        }
    }, []);


    useEffect(() => {
        // 1.     
        const handleStatusChange = () => setIsOnline(navigator.onLine);
        window.addEventListener('online', handleStatusChange);
        window.addEventListener('offline', handleStatusChange);

        // 2.     Axios (     IP)
        const interceptor = axiosClient.interceptors.response.use(
            response => response,
            error => {
                if (!error.response) { //   (Network Error)
                    setIsServerAvailable(false);
                }
                return Promise.reject(error);
            }
        );

        return () => {
            window.removeEventListener('online', handleStatusChange);
            window.removeEventListener('offline', handleStatusChange);
            axiosClient.interceptors.response.eject(interceptor);
        };
    }, []);

    const handleRetry = () => {
        setIsServerAvailable(true);
        window.location.reload();
    };

    if (loading) {
        return <LoadingScreen />;
    }

    //     
    if (!isOnline || !isServerAvailable) {
        return (
            <Box sx={{ height: '100vh' }}>
                <OfflineStub onRetry={() => window.location.reload()} />
            </Box>
        );
    }

    return (
        <Routes>
            {/*      /login     */}
            <Route path="/login" element={!isAuthenticated ? <LoginPage /> : <Navigate to="/dashboard" />} />

            {/*   */}
            <Route path="/dashboard/*" element={<PrivateRoute><DashboardPage /></PrivateRoute>} />

            {/* :       */}
            <Route path="/" element={<Navigate to={isAuthenticated ? "/dashboard" : "/login"} />} />

            {/*      */}
            <Route path="*" element={<Navigate to="/" />} />
        </Routes>
    );
};

const App: React.FC = () => {
    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <Router>
                <AuthProvider>
                    <AppContent />
                </AuthProvider>
            </Router>
        </ThemeProvider>
    );
};

export default App;